import type { Metadata } from "next";
import NotesClient from './Notes.client';
import { HydrationBoundary, dehydrate, QueryClient } from "@tanstack/react-query";
import { fetchNotes } from "@/lib/api";


type Props = {
  params: {
    slug?: string[] | Promise<string[]>;
  };
};

export async function generateMetadata({ params }: Props): Promise<Metadata> {
  const slugArray = Array.isArray(params.slug) ? params.slug : await params.slug;
  const filter = slugArray?.[0] || "all";
  const filterName = filter === "all" ? "All Notes" : `Notes filtered by "${filter}"`;

  const title = `${filterName} - NoteHub`;
  const description = `Browse and manage your ${filter === "all" ? "all notes" : `notes filtered by "${filter}"`} in NoteHub.`;

  return {
    title,
    description,
    openGraph: {
      title,
      description,
      url: `https://07-routing-nextjs-phi-black.vercel.app/notes/filter/${filter}`,
      images: [
        {
          url: "https://ac.goit.global/fullstack/react/notehub-og-meta.jpg",
          width: 1200,
          height: 630,
          alt: "NoteHub â€” filtered notes",
        },
      ],
      type: 'article',
    }
  }
}

export default async function NotesByTag({ params }: Props) {
  const slugArray = Array.isArray(params.slug) ? params.slug : await params.slug;
  const tag = slugArray?.[0] === "all" ? undefined : slugArray?.[0];

  return <NotesClient tag={tag} />;
}
